  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.3);
}
.modal-content {
  background-color: var(--color-background);
  margin: 100px auto;
  padding: 20px 30px;
  border-radius: 12px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 4px 18px rgba(0, 0, 0, 0.1);
  position: relative;
}
.modal-header {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 20px;
  color: #000;
}

.close-button {
  position: absolute;
  top: 15px;
  right: 20px;
  color: #aaa;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
}

.close-button:hover,
.close-button:focus {
  color: #000;
}

#new-person-name {
  width: 100%;
  padding: 12px 16px;
  margin-bottom: 20px;
  font-size: 16px;
  border: 1px solid #ced4da;
  border-radius: 8px;
  outline: none;
  color: var(--color-text);
  background-color: var(--color-background);
}

#new-person-name:focus {
  border-color: #66afe9;
}

#create-chat-confirm {
  background-color: #0088cc;
  color: #fff;
  border: none;
  padding: 12px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 8px;
  width: 100%;
}

#create-chat-confirm:hover {
  background-color: #007ab8;
}

@media screen and (max-width: 480px) {
  .modal-content {
    margin: 50px auto;
    width: 90%;
  }
